import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { SaveService } from '@app/core/http/save.service';
import { Package } from '@app/shared/models/package';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-packages-create',
  templateUrl: './packages-create.component.html',
  styleUrls: ['./packages-create.component.css']
})
export class PackagesCreateComponent implements OnInit {
  public submitted: boolean = false;

  createForm: FormGroup = this.formBuilder.group({
    name: ["", [Validators.required]],
    activeDays: [""],
    noOfMockTest: [0],
    excludeGstPrice: ["", Validators.required],
    includeGstPrice: ["", Validators.required],
    speakingLimit: [""],
    type: ["", Validators.required],
    showPrice: [""]
  });

  public sub: Subscription;
  public packageId: string;

  constructor(private formBuilder: FormBuilder,
    private _saveService: SaveService, private _activeRoute: ActivatedRoute) { }

  ngOnInit() {
    let data = this._activeRoute.snapshot.data['packageDetail'];
    if (data)
      this.getDataEditForm(data);
  }


  getDataEditForm(data: Package) {
    this.packageId = data.packageId;

    this.createForm = this.formBuilder.group({
      name: [data.name, [Validators.required]],
      activeDays: [data.activeDays],
      noOfMockTest: [data.noOfMockTest],
      excludeGstPrice: [data.excludeGstPrice, Validators.required],
      includeGstPrice: [data.includeGstPrice, Validators.required],
      speakingLimit: [data.speakingLimit],
      type: [data.type, Validators.required],
      showPrice: [data.showPrice]
    });
  }

  get f() {
    return this.createForm.controls;
  }

  onSave() {
    this.submitted = true;
    if (this.createForm.invalid) {
      return
    }
    this.sub = this._saveService.onSavePackage(this.createForm.value, this.packageId, this.createForm.value['type'])
      .subscribe((res) => {
        this.goBack()
      })
  }

  goBack() {
    this._saveService.goBackPrev();
  }
}
