import { NgModule } from '@angular/core';
import { Routes, RouterModule, Route } from "@angular/router";
import { PackagesCreateComponent } from '@app/modules/manage/packages/packages-create/packages-create.component';
import { PackagesComponent } from '@app/modules/manage/packages/packages/packages.component';
import { PackageDetailReslover } from '@app/modules/manage/reslovers/package-detail.reslover';




const routes: Routes = [
    {
        path: "",
        component: PackagesComponent,
    },
    {
        path: "create",
        component: PackagesCreateComponent,
    },
    {
        path: "create/:detailId",
        component: PackagesCreateComponent,
        resolve: {
            packageDetail: PackageDetailReslover
        }
    },
];


@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
    providers: [
        PackageDetailReslover
    ]
})

export class PackageRoutingModule { }
