import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TableListService } from '@app/core/http/serviceIndex';
import { Package, PackageList } from '@app/shared/models/package';
import { Practise, PractiseList } from '@app/shared/models/practise';
import { TableFilter } from '@app/shared/models/table-filter';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-packages',
  templateUrl: './packages.component.html',
  styleUrls: ['./packages.component.css']
})
export class PackagesComponent implements OnInit {

  public moduleName:string;

  public tableFilter=<TableFilter>{};

  public packages:Package[];

  private subscription:Subscription;
  private isAsc:boolean=true;

  isLoading:boolean=false;

  constructor(private _tableService:TableListService,private _activeRoute:ActivatedRoute) { }

  ngOnInit() {
    this.isLoading=true;
    this._activeRoute.queryParams.subscribe(
      (params) => {
        if (params.page) {
          this.tableFilter.page = params.page;
          this.tableFilter.limit = params.limit;
        } else if (params.search) {
          this.tableFilter.search = params.search;
        } else{
          this.tableFilter.page = 1;
          this.tableFilter.limit = 10;
          this.tableFilter.sortDirection = 'desc';
          this.tableFilter.sortColumn = 'name';
          this.tableFilter.search=''
        }
        this.getPractiseList();
      }
    )
  }

  getPractiseList(){
    this.subscription=this._tableService.getManagePackages(this.tableFilter)
    .subscribe((res:PackageList)=>{
      this.tableFilter=res.filter;
      this.packages=res.storePackages;
      this.isLoading=false;
    })
  }
  
  fitter(sortColumn) {
    this.isLoading=true;
    this.isAsc = !this.isAsc ? this.isAsc = true : this.isAsc = false;
    this.tableFilter.sortDirection = this.isAsc ? 'asc' : 'desc';
    this.tableFilter.sortColumn = sortColumn;
    this.getPractiseList();
  }

  deletePackage(data){
    this.subscription=this._tableService.onDeletePackage(data.packageId,data.type)
    .subscribe((res)=>{
      this.getPractiseList();
    })
  }
  
  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    if(this.subscription) this.subscription.unsubscribe();
  }

}
